<template>
    <div id="caihui">
      <div id="loadingDiv" v-show="loadingDivShow">
        <div class="curtain" :style="loadingDivCurtain"></div>
        <div class="loadingDiv" v-show="loadingShow">
          <img class="logo" src="./img/loading/logo.png"/>
          <img class="gif" src="./img/loading/loading.gif">
          <p>{{loadingValue}}%</p>
        </div>
        <img v-show="!loadingShow" class="touchTips" src="./img/touchTips.png"/>
      </div>
      <guaguaka v-if="guaguakaShow" ref="guaguaka" :guaguakaConfig="guaguakaConfig" id="caihuiDiv" @success="success" @hideLoading="hideLoading"></guaguaka>
      <div id="animation" v-if="animationShow" :style="{width: guaguakaConfig.width, height: guaguakaConfig.height, backgroundImage: 'url(' + animationImg + ')'}"></div>
      <div id="endDiv" v-show="endDivShow">
        <div class="bordertop border"></div>
        <div class="borderContent">
          <img src="./img/endPage/endContent.png">
        </div>
        <div class="borderBottom border"></div>
      </div>
    </div>
</template>

<script>
import $ from 'jquery'
import guaguaka from '@/views/myPlugin'
import prizeImg from './img/prizeImg.png'
import curtainImg from './img/curtainImg.png'
import touchImg from './img/touchImg.png'
import animationImg from './img/hua.png'
import loadinglogo from './img/loading/logo.png'
import loadingGif from './img/loading/loading.gif'
import endContent from './img/endPage/endContent.png'
import border from './img/endPage/border.png'
import borderBottom from './img/endPage/borderBottom.png'
import borderTop from './img/endPage/borderTop.png'
import luckDrawMp3 from './audio/yuyin.mp3'
import audioPlayer from '@/utils/audio.js'
import downloadImg from '@/utils/downloadImg.js'
export default {
  name: 'index',
  components: {
    guaguaka
  },
  data () {
    return {
      windowWidth: document.documentElement.style.fontSize.split('px')[0],
      guaguakaConfig: {
        id: 'caihuiDiv',
        width: '7.5rem',
        height: '12.6rem',
        visibleArea: 55,
        curtainImg: curtainImg,
        curtainHide: true,
        prizeImg: prizeImg,
        isTouch: true,
        touchSpotShow: true,
        touchSpotImg: touchImg,
        touchSpotSize: '0.5rem',
        touchSpotImgWidth: '1.16rem',
        touchSpotImgHeight: '1.56rem',
        touchSpotPosition: '0.26 0.15'
      },
      loadingDivCurtain: {
        backgroundColor: '#FDE9D0',
        opacity: 1
      },
      loadingValue: 0,
      luckDrawMp3: new audioPlayer.SoundComponent({src: luckDrawMp3, altSrc: luckDrawMp3}),
      animationImg: animationImg,
      loadingDivShow: true,
      loadingShow: true,
      guaguakaShow: false,
      animationShow: false,
      endDivShow: false
    }
  },
  mounted () {
    const that = this
    let loadingNb = 0
    const imageArray = [loadinglogo, loadingGif, animationImg, curtainImg, prizeImg, touchImg, endContent, border, borderBottom, borderTop]
    downloadImg.preloadimages(imageArray).done(function (images) {
      loadingNb++
      if (loadingNb > 1) {
        that.loadingValue = parseInt(loadingNb / imageArray.length * 100)
        if (that.loadingValue === 100) {
          $('#loadingDiv').on('touchstart', function () {
            if (!that.loadingShow && $('#loadingDiv').attr('class') !== 'hide') {
              $('#loadingDiv').attr('class', 'hide')
              that.luckDrawMp3.play()
              setTimeout(function () {
                that.loadingDivShow = false
              }, 2100)
            }
          })
          const innerHeight = document.documentElement.clientHeight > document.documentElement.clientWidth ? document.documentElement.clientHeight : document.documentElement.clientWidth
          that.guaguakaConfig.width = innerHeight < 12.06 * that.windowWidth ? (innerHeight / that.windowWidth / 12.06 * 7.5) + 'rem' : '7.5rem'
          that.guaguakaConfig.height = innerHeight < 12.06 * that.windowWidth ? (innerHeight / that.windowWidth) + 'rem' : '12.06rem'
          that.guaguakaShow = true
        }
      }
    })
  },
  methods: {
    hideLoading () {
      const that = this
      this.loadingValue = 100
      setTimeout(function () {
        that.loadingShow = false
        that.loadingDivCurtain = {
          backgroundColor: '#000000',
          opacity: 0.8
        }
      }, 800)
    },
    success (id) {
      const that = this
      setTimeout(function () {
        that.setAnimation()
        $('#caihuiDiv').attr('class', 'hide1')
        setTimeout(function () {
          that.guaguakaShow = false
        }, 2000)
        setTimeout(function () {
          $('#animation').attr('class', 'hide1')
          // that.animationShow = false
        }, 5500)
        setTimeout(function () {
          that.endDivShow = true
          $('#endDiv').attr('class', 'show')
        }, 6800)
      }, 1500)
    },
    setAnimation () {
      this.animationShow = true
      let animationPosition = 0
      const mysetInterval = setInterval(function () {
        animationPosition = animationPosition - 7.5
        if (animationPosition < -255) {
          animationPosition = -255
          clearInterval(mysetInterval)
        }
        $('#animation').css({
          backgroundPosition: animationPosition + 'rem 0'
        })
      }, 135)
    }
  }
}
</script>

<style scoped lang="scss">
  #caihui{
    p{
      margin: 0;
    }
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    #loadingDiv{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 200;
      display: flex;
      align-items: center;
      justify-content: center;
      .curtain{
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        /*background-color: black;*/
      }
      .loadingDiv{
        position: absolute;
        text-align: center;
        font-size: 0.4rem;
        color: #f3b56d;
        .logo{
          width: 5rem;
          display: block;
        }
        .gif{
          width: 1rem;
          display: block;
          margin: 0.7rem auto 0.1rem;
        }
      }
     .touchTips{
       position: absolute;
       width: 3.64rem;
       height: 3.07rem;
     }
    }
    #caihuiDiv{
      margin: auto;
    }
    #animation{
      position: absolute;
      width: 7.5rem;
      height: 12.06rem;
      background-size: 255rem 12.06rem;
      background-repeat: no-repeat;
      z-index: 200;
    }
    #endDiv{
      position: absolute;
      font-size: 0;
      z-index: 300;
      opacity: 0;
      .border{
        height: 1.06rem;
        width: 100%;
        background-size: 100% 100%;
        background-repeat: no-repeat;
      }
      .bordertop{
        background-image: url("./img/endPage/borderTop.png");
      }
      .borderBottom{
        background-image: url("./img/endPage/borderBottom.png");
      }
      .borderContent{
        background-image: url("./img/endPage/border.png");
        background-size: 100%;
        padding: 0.18rem 0.97rem 0.3rem;
        height: calc(100vh - 2.4rem);
        overflow: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        img{
          width: 100%;
        }
      }
    }
    .hide{
      animation: hide 0.7s;
      animation-fill-mode: forwards;
    }
    .hide1{
      animation: hide 2s;
      animation-fill-mode: forwards;
    }
    .show{
      animation: show 3s;
      animation-fill-mode: forwards;
    }
    @keyframes hide {
      0% {opacity: 1;}
      10% {opacity: 0.9;}
      20% {opacity: 0.8;}
      30% {opacity: 0.7;}
      40% {opacity: 0.6;}
      50% {opacity: 0.5;}
      60% {opacity: 0.4;}
      70% {opacity: 0.3;}
      80% {opacity: 0.2;}
      90% {opacity: 0.1;}
      100% {opacity: 0;}
    }
    @keyframes show {
      0% {opacity: 0;}
      10% {opacity: 0.1;}
      20% {opacity: 0.2;}
      30% {opacity: 0.3;}
      40% {opacity: 0.4;}
      50% {opacity: 0.5;}
      60% {opacity: 0.6;}
      70% {opacity: 0.7;}
      80% {opacity: 0.8;}
      90% {opacity: 0.9;}
      100% {opacity: 1;}
    }
  }
</style>



