<template>
  <div id="app">
    <router-view/>
    <div class="horizontalScreenHint" v-show="horizontalScreenHintShow">
      <div class="horizontalScreenHintDiv">
        <img src="./assets/hengPing.png">
        <p>为了更好的体验,为了开发的幸福,为了世界的和平,请各位大帅哥小姐姐锁定竖屏观看及操作本页面</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      horizontalScreenHintShow: false
    }
  },
  mounted () {
    this.changeOrientation()
    this.changeResize()
  },
  methods: {
    changeOrientation () {
      const that = this
      const width = document.documentElement.clientWidth
      const height = document.documentElement.clientHeight
      if (width > height) {
        // 横屏
        that.horizontalScreenHintShow = true
      }
      const evt = 'onorientationchange' in window ? 'orientationchange' : 'resize'
      window.addEventListener(evt, function () {
        that.changeResize()
        setTimeout(function () {
          const width = document.documentElement.clientWidth
          const height = document.documentElement.clientHeight
          if (width > height) {
            // 横屏
            that.horizontalScreenHintShow = true
          } else {
            // 竖屏
            that.horizontalScreenHintShow = false
          }
        }, 300)
      }, false)
    },
    changeResize () {
      const doc = document.documentElement
      const windowWidth = window.screen.width > window.screen.height ? window.screen.height : window.screen.width
      const width = Math.min(windowWidth, 768)
      if (!width) return
      doc.style.fontSize = 100 * (width / 750) + 'px'
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.horizontalScreenHint{
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  text-align: center;
  background-color: #000000;
  z-index: 1000;
}
.horizontalScreenHintDiv{
  position: absolute;
  top: 50%;
  width: 90%;
  height: 130px;
  left: 0;
  right: 0;
  margin: -75px auto 0;
}
.horizontalScreenHint p{
  font-size: 0.2rem;
  color: #ffffff;
  margin:  14px auto 0;
  text-align: center;
}
</style>



